/// Zappar for ThreeJS Examples
/// Play animation from gaze

// In this image tracked example we'll use a THREE.Raycaster to detect if
// the user is pointing their camera at a 3D object. If it is, we'll play
// the animation that's baked into the 3D model file.

import * as ZapparThree from '@zappar/zappar-threejs';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader';

 let screenLock:any = null;
 navigator.wakeLock.request('screen')
    .then(lock => { 
      screenLock = lock; 
 });
 
document.addEventListener('visibilitychange', async () => {
  if (screenLock !== null && document.visibilityState === 'visible') {
    if(screenLock)
    screenLock = await navigator.wakeLock.request('screen');
  }
});
const loader = new DRACOLoader();
loader.setDecoderPath('https://www.gstatic.com/draco/versioned/decoders/1.5.6/')
loader.preload()
// const targetImage = new URL('../assets/QR_Pere2.zpt', import.meta.url).href;
const targetImage = new URL('../assets/qr-code1.zpt', import.meta.url).href;
const model_Male = new URL('../assets/MB.W222.AMG5.glb', import.meta.url).href;
// const model_Female = new URL('../assets/female.glb', import.meta.url).href;
// const female_Voice = new URL('../assets/Voix-off-female.mp3',import.meta.url).href
// const male_Voice = new URL('../assets/Voix-off-male.mp3',import.meta.url).href

// The SDK is supported on many different browsers, but there are some that
// don't provide camera access. This function detects if the browser is supported
// For more information on support, check out the readme over at
// https://www.npmjs.com/package/@zappar/zappar-threejs
if (ZapparThree.browserIncompatible()) {
  // The browserIncompatibleUI() function shows a full-page dialog that informs the user
  // they're using an unsupported browser, and provides a button to 'copy' the current page
  // URL so they can 'paste' it into the address bar of a compatible alternative.
  ZapparThree.browserIncompatibleUI();

  // If the browser is not compatible, we can avoid setting up the rest of the page
  // so we throw an exception here.
  throw new Error('Unsupported browser');
}

// ZapparThree provides a LoadingManager that shows a progress bar while
// the assets are downloaded. You can use this if it's helpful, or use
// your own loading UI - it's up to you :-)
const manager = new THREE.LoadingManager();
// const listener = new THREE.AudioListener();
// const maleSound = new THREE.Audio(listener);
// const femaleSound = new THREE.Audio(listener);
// const audioLoader = new THREE.AudioLoader();
// audioLoader.load(male_Voice, function(buffer) {
//   maleSound.setBuffer(buffer);
