// Construct our ThreeJS renderer and scene as usual
const renderer = new THREE.WebGLRenderer({ antialias: true });
const scene = new THREE.Scene();
renderer.outputEncoding = THREE.sRGBEncoding;
renderer.physicallyCorrectLights = true;
renderer.toneMapping = THREE.ACESFilmicToneMapping;
renderer.toneMappingExposure = 1.5; // You can tweak this (0.8 to 1.5) based on brightness
document.body.appendChild(renderer.domElement);
const ambientLight = new THREE.AmbientLight(0xffffff, 15); // Lower intensity to blend with other lights
scene.add(ambientLight);

// Directional light - mimics sunlight
const directionalLight = new THREE.DirectionalLight(0xffffff, 5);
directionalLight.position.set(5, 10, 7.5);
directionalLight.castShadow = true;
scene.add(directionalLight);

// Hemisphere light - simulates sky and ground light
const hemisphereLight = new THREE.HemisphereLight(0xaaaaaa, 0x444444, 5);
hemisphereLight.position.set(0, 20, 0);
scene.add(hemisphereLight);
// const directionalLight = new THREE.DirectionalLight(0xffffff, 5);
// directionalLight.position.set(5, 10, 7.5);
// scene.add(directionalLight);
// renderer.outputEncoding = THREE.sRGBEncoding;
// renderer.toneMapping = THREE.ACESFilmicToneMapping;
// renderer.toneMappingExposure = 1.5;
// As with a normal ThreeJS scene, resize the canvas if the window resizes
renderer.setSize(window.innerWidth, window.innerHeight);
window.addEventListener('resize', () => {
  renderer.setSize(window.innerWidth, window.innerHeight);
});

// Create a Zappar camera that we'll use instead of a ThreeJS camera
const camera = new ZapparThree.Camera({

});

// In order to use camera and motion data, we need to ask the users for permission
// The Zappar library comes with some UI to help with that, so let's use it
ZapparThree.permissionRequestUI().then((granted) => {
  // If the user granted us the permissions we need then we can start the camera
  // Otherwise let's them know that it's necessary with Zappar's permission denied UI
  if (granted) camera.start();
  else ZapparThree.permissionDeniedUI();
});

// The Zappar component needs to know our WebGL context, so set it like this:
ZapparThree.glContextSet(renderer.getContext());

// Set the background of our scene to be the camera background texture
// that's provided by the Zappar camera
scene.background = camera.backgroundTexture;

// Set an error handler on the loader to help us check if there are issues loading content.
manager.onError = (url) => console.log(`There was an error loading ${url}`);

// Create a zappar ImageTracker and wrap it in an ImageAnchorGroup for us
// to put our ThreeJS content into
// Pass our loading manager in to ensure the progress bar works correctly
const imageTracker = new ZapparThree.ImageTrackerLoader(manager).load(targetImage);
// const imageTracker2 = new ZapparThree.ImageTrackerLoader(manager).load(targetImage2);
const imageTrackerGroup = new ZapparThree.ImageAnchorGroup(camera, imageTracker);
// const imageTrackerGroup2 = new ZapparThree.ImageAnchorGroup(camera, imageTracker2);

// Add our image tracker group into the ThreeJS scene
scene.add(imageTrackerGroup);
// scene.add(imageTrackerGroup2);

let action: THREE.AnimationAction;
// let action2: THREE.AnimationAction;
let mixer: THREE.AnimationMixer | null = null;
let clipActions: THREE.AnimationAction[] = [];
// Load a 3D model to place within our group (using ThreeJS's GLTF loader)
// Pass our loading manager in to ensure the progress bar works correctly
const gltfLoader = new GLTFLoader(manager);
gltfLoader.setDRACOLoader(loader);

// In your GLTF loader:
gltfLoader.load(model_Male, (gltf) => {
  gltf.scene.scale.set(0.2, 0.2, 0.2);  
  console.log(gltf);
  
  mixer = new THREE.AnimationMixer(gltf.scene);
  clipActions = gltf.animations.map((clip) => {
    const clipAction = mixer!.clipAction(clip);
    clipAction.setLoop(THREE.LoopOnce);
    clipAction.clampWhenFinished = true;
    clipAction.paused = true; // Start paused
    return clipAction;
  });
  
  imageTrackerGroup.add(gltf.scene.rotateX(Math.PI / 2));
}, undefined, () => {
  console.log('An error ocurred loading the GLTF model');
});

// Update your visibility handlers:
imageTracker.onVisible.bind(() => {
  scene.visible = true;
  
  if (mixer) {
    mixer.stopAllAction(); // Stop any running actions
    clipActions.forEach(action => {
      action.reset().play(); // Reset and play each action
    });
  }
});

imageTracker.onNotVisible.bind(() => {
  scene.visible = false;
  
  if (mixer) {
    // Don't stop the actions - let them finish naturally
    // mixer.stopAllAction();
  }
});


