// used to get deltaTime for our animations.
const clock = new THREE.Clock();


// Use a function to render our scene as usual
function render(): void {
  // The Zappar camera must have updateFrame called every frame
  camera.updateFrame(renderer);

  // Point the raycaster down the center of the camera

  // Set up raycasting
  const raycaster = new THREE.Raycaster();
  const pointer = new THREE.Vector2(0, 0); // Center of the screen
  raycaster.setFromCamera(pointer, camera);

  // Check for intersections
  const intersectsMale = raycaster.intersectObject(imageTrackerGroup, true);
  // const intersectsFemale = raycaster.intersectObject(imageTrackerGroup2, true);

  // Play animations based on gaze
  if (intersectsMale.length > 0) {
    if (action && !action.isRunning()) {
      action.reset().play(); // Play the male animation
    }
  }
  const delta = clock.getDelta();
  if (mixer) mixer.update(delta);
  // if (mixer2) mixer2.update(delta);
  // Draw the ThreeJS scene in the usual way, but using the Zappar camera
  renderer.render(scene, camera);

  // Call render() again next frame
  requestAnimationFrame(render);
}
  render();



// Start things off
